@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);

  
  body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* background-image: url('https://images.unsplash.com/photo-1568832359672-e36cf5d74f54?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max'); */
    background-color: rgba(78, 105, 179, 0.288);
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }

  .soLink:hover {
    box-shadow: 0px 0px 3px rgb(255, 255, 255) !important;
  }

  

  .navLink:hover {
    color: rgb(56, 56, 55) !important;
    text-decoration:underline;
    text-underline-offset: 4px;
  }

  .tm1 {
    font-family:Sacramento;
    font-size:33px;
    color: white;
  }

  .tm2 {
    font-family:Sacramento;
    font-size:30px;
  }

  .td {
    color: rgb(255, 255, 255);
    font: 2em sans-serif;
  }
  
  .titleBanner {
    background-color: rgba(43, 68, 139, 0.712);
  outline: dashed rgba(255, 255, 255, 0.849) 1px;
    outline-offset: -6px;
    border-bottom: solid rgba(0, 0, 0, 0.342) 1px;
    border-top: solid rgba(255, 255, 255, 0.349) 1px;

  }

  .titleBanner2 {
    background-color: rgb(218, 196, 143);
    outline: dashed rgba(64, 62, 182, 0.836) 1px;
    outline-offset: -4px;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.555) !important;
    color: rgb(0, 0, 0);
  }

  .titleBanner2:hover{
    background-color: rgb(182, 171, 121);
    text-decoration: none;
    box-shadow: 0px -1px 2px rgba(0, 0, 0, 0.555) !important;
    color: rgb(255, 255, 255) !important;
  }

  .titleBanner3 {
    background-color: rgb(182, 171, 121);

    outline: dashed rgba(64, 62, 182, 0.836) 1px;
    outline-offset: -12px;
  }

  .nav2{
    border-bottom: solid rgba(0, 0, 0, 0.267) 1px;
    border-top: solid rgba(212, 128, 128, 0.096) 2px;
    outline: dashed rgba(255, 255, 255, 0.925) 1px;
    outline-offset: -6px;
    background-color: rgba(206, 71, 71, 0.904);
  }

 
  .tnav {
    border-bottom: solid rgba(0, 0, 0, 0.137) 1px;
    outline: dashed rgba(255, 255, 255, 0.925) 1px;
    outline-offset: -6px;
    background-color: rgba(206, 71, 71, 0.904);
  }
